import { Component, Input, Output, EventEmitter } from '@angular/core';
import { ROUTER_DIRECTIVES, Router } from '@angular/router';

@Component({
    selector: 'tree-view',
    templateUrl: './app/gallery/components/tree.view.html',
    directives: [TreeView, ROUTER_DIRECTIVES]
})

export class TreeView {
    @Input() nodes: any[];
    @Output() onNotify: EventEmitter<number> = new EventEmitter<number>();
    selectedNode: any;

    constructor() {
    }

    onClick(node: any) {
      console.log(node);
      this.onNotify.emit(node.id);
      this.onNotifyChild(node.id);
    }

    onNotifyChild(node: any): void {
      this.onNotify.emit(node.id);
    }
}
